.sidebar {
    width: 250px;
    transition: width 0.3s ease;
  }
  
  .sidebar.close {
    width: 70px;
  }
  
  .nav-list {
    list-style: none;
    padding: 0;
  }
  
  .menu-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    cursor: pointer;
  }
  
  .submenu {
    list-style: none;
    padding: 0 20px;
    display: none;
  }
  
  .submenu.show {
    display: block;
  }
  
  .sidebar-btn {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 1.2rem;
    margin: 10px;
  }
  .simplebar-content{
    background-color: teal !important;
    color: white !important;
  }
  .MuiTypography-subtitle2 {
    color: white !important;
  }
  .MuiTypography-body2{
    color: white !important;
  }
  .MuiPaper-elevation8{
    background-color: teal !important;
    color: white !important;
  }